import React from 'react'
import avatarPng from '../../../assets/image/ivan_ivanov.png'


export const Users = () => {
    return (
        <div
            className={`UsersInviteds__item`}
        >
            <div className="UsersInviteds__user">
                <div className="UsersInviteds__user-avatar">
                    <img src={avatarPng} alt="Avatar" />
                </div>
                <div className="UsersInviteds__user-info">
                    <div className="UsersInviteds__user-name">Иван Иванов</div>
                    <div className="UsersInviteds__user-email">Ivan777Ivanov@gmail.com</div>
                </div>
            </div>
            <div className="UsersInviteds__checked"></div>
        </div>
    )
}
