import React, { useEffect } from 'react'
import { Header, SignIn } from './components'
import { Switch, Route} from 'react-router-dom'
import { Account, AddIndited, Cpa, CpaAdd, Main, Payouts } from './pages'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { RootState } from './store/reducers/rootReducer'
import { Swipe } from './hoc/Swipe'

export const App = () => {
    const { username } = useSelector(({ user }: RootState) => user)
    const history = useHistory()
    // const pathUrl = ['/', '/cpa', '/payouts', '/account']
    // const location = useLocation()



    // const { ref } = useSwipeable({
    //     delta: 150,
    //     onSwipedLeft: () => {
    //         if (pathUrl[pathUrl.length - 1] === location.pathname) {
    //             history.push(pathUrl[0])
    //         }
    //         history.push(pathUrl[pathUrl.indexOf(location.pathname) + 1])
    //     },
    //     onSwipedRight: () => {
    //         if (pathUrl[0] === location.pathname) {
    //             history.push(pathUrl[pathUrl.length - 1])
    //         }
    //         history.push(pathUrl[pathUrl.indexOf(location.pathname) - 1])
    //     }
    // }) as { ref: RefCallback<Document> }

    // useEffect(() => {
    //     ref(document)
    // })

    useEffect(() => {
        if (username === null) {
            history.push('/')
        }
    })

    return (
        <>
            <Header />
            <div className="wrapper">
                {username === null ? <SignIn /> :
                    <Swipe>
                        <Switch>
                            <Route path='/' exact component={Main} />
                            <Route path='/payouts' component={Payouts} />
                            <Route path='/cpa' exact component={Cpa} />
                            <Route path='/cpa/add' component={CpaAdd} />
                            <Route path='/account' component={Account} />
                            <Route path='/addinvited' component={AddIndited} />
                        </Switch>
                    </Swipe>
                }
            </div>
        </>
    )
}
