import React from 'react'
import { useSelector } from 'react-redux'
import { Link, NavLink } from 'react-router-dom'
import Logo from '../../assets/image/Logo.png'
import { RootState } from '../../store/reducers/rootReducer'
import './Header.scss'
export const Header = () => {
    const { username } = useSelector(({ user }: RootState) => user)
    return (
        <div className='Header'>
            <div className="Header__inner">
                <div className="Header__logo">
                    <Link to="/"><img src={Logo} alt="BlogFly" /></Link>
                </div>
                <div className="Header__nav">
                    <ul className="Header__nav-list">
                        {username !== null && (username === 'admin' ?
                            <>
                                <li className="Header__nav-li"><NavLink exact to='/'>Главная</NavLink></li>
                                <li className="Header__nav-li"><NavLink to='/cpa'>CPA</NavLink></li>
                                <li className="Header__nav-li"><NavLink to='/payouts'>Выплаты</NavLink></li>
                            </>
                            :
                            <>
                                <li className="Header__nav-li"><NavLink exact to='/'>Главная</NavLink></li>
                                <li className="Header__nav-li"><NavLink to='/cpa'>CPA</NavLink></li>
                                <li className="Header__nav-li"><NavLink to='/account'>Аккаунт</NavLink></li>
                            </>)}
                    </ul>
                </div>
            </div>
        </div>
    )
}
