import React from 'react'
import './UsersInviteds.scss'
import { Users } from './Users/Users'
import { Link } from 'react-router-dom'

export const UsersInviteds = () => {

    return (
        <div className="UsersInviteds">
            <div className="UsersInviteds__inner">
                <div className="UsersInviteds__top">
                    <div className="UsersInviteds__search">
                        <div className="UsersInviteds__search-filter">Фильтры</div>
                        <div className="UsersInviteds__search-search">
                            <input type="text" name="" id="" placeholder="Поиск" />
                        </div>
                    </div>
                    <div className="UsersInviteds__all">А-Я</div>
                </div>
                <div className="UsersInviteds__items">
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                    <Users />
                </div>
                <Link className="UsersInviteds__invite" to='addinvited'>Пригласить</Link>
            </div>
        </div>
    )
}
