import React, { useEffect, useState } from 'react'
import Calendar from 'react-calendar'
import imgTest from '../../../assets/image/img1.png'
import axios from 'axios'

import './CpaAdd.scss'
import './calendar.scss'

export const CpaAdd = () => {
    const [activeCalendar, setActiveCalendar] = useState(false)
    const [dateCalendar, setDateCalendar] = useState<any>(new Date())
    const [img, setImg] = useState<any>({
        file: null,
        imagePreviewUrl: null
    })
    const [date, setDate] = useState({
        firstDate: new Date().toLocaleDateString(),
        secondDate: new Date().toLocaleDateString()
    })

    console.log(img.file);


    const onImageChange = async (event: any) => {
        event.preventDefault()
        if (event.target.files && event.target.files[0]) {
            let img = event.target.files[0]
            let reader = new FileReader()
            reader.readAsDataURL(img)
            reader.onload = () => {
                setImg({
                    file: img,
                    imagePreviewUrl: reader.result
                })

            };

        }

    };

    const formHandLerSubmit: React.FormEventHandler = (e) => {
        e.preventDefault()
        console.log(img.file);

        axios.get('http://blogfly-local.com:8000/api/v1/cpa/manage/')
            .then((response) => console.log(response))
            .catch((err) => console.log(err))
    }
    useEffect(() => {
        if (dateCalendar.length === 2) {
            setDate(prev => {
                return {
                    ...prev,
                    firstDate: dateCalendar[0].toLocaleDateString(),
                    secondDate: dateCalendar[1].toLocaleDateString()
                }
            })
        }
    }, [dateCalendar])

    return (
        <div className="CpaAdd">
            <div className="CpaAdd__inner">
                <form className="CpaAdd__form" onSubmit={(e) => formHandLerSubmit(e)}>
                    <div className="CpaAdd__top">
                        <div className="CpaAdd__top-img">
                            <input type="file" accept=".png, .jpg, .jpeg" name="name" id="name" placeholder={'hello'} onChange={(e) => onImageChange(e)} />
                            <label htmlFor="name">
                               {img.imagePreviewUrl && <img src={img.imagePreviewUrl} alt="" />} 
                            </label>
                        </div>
                        <div className="CpaAdd__top-name">
                            <label htmlFor="">Название</label>
                            <input type="text" name="" id="" placeholder="Введите название" />
                        </div>
                    </div>
                    <div className="CpaAdd__textarea">
                        <label htmlFor="">Условия</label>
                        <textarea placeholder="Введите условия" name="" id="" cols={30} rows={10}></textarea>
                    </div>
                    <div className="CpaAdd__date">
                        <div className="CpaAdd__date-title">Дата</div>
                        <div
                            className="CpaAdd__date-top"
                            onClick={() => setActiveCalendar(prev => !prev)}
                        >
                            <div>{date.firstDate}</div>
                            <div></div>
                            <div>{date.secondDate}</div>
                        </div>
                        <div className={`CpaAdd__date-calendar${activeCalendar ? ' active' : ''}`}>
                            <Calendar
                                onChange={setDateCalendar}
                                value={dateCalendar}
                                selectRange={true}
                                allowPartialRange={true}
                                defaultView={"month"}
                                prev2Label={null}
                                next2Label={null}
                            />
                        </div>
                    </div>
                    <div className="CpaAdd__money">
                        <input type="type" placeholder='___' maxLength={3} />
                        <label htmlFor="">$</label>
                    </div>
                    <button type="submit" className="CpaAdd__submit">Сохранить</button>
                </form>
            </div>



        </div>
    )
}
