import React, { useState } from 'react'
import { Bar } from 'react-chartjs-2'
import { useSelector } from 'react-redux'
import { RootState } from '../../../store/reducers/rootReducer'

import './BarChart.scss'




const options = {
    maintainAspectRatio : false,
    scales: {
        yAxes: [{
            ticks: {
                fontSize: 0 
            }
        }],
        xAxes: [{
            stacked: true,
            categoryPercentage: 1.1,
            ticks: {
                fontSize: 16,
                FontFamily: 'BellotaText',
            }
        }]
    },
    legend: {
        display: false
    },
    elements: {
        point: {
            radius: 10,
            hoverRadius: 10,
            hoverBorderWidth: 3
        }
    },
    tooltips: {
        mode: 'index',
        titleFontSize: 0,
        bodyFontFamily: 'BellotaText',
        bodyFontSize: 22,
        fontWeight: 700,
        bodyFontColor: '#424242',
        displayColors: false,
        backgroundColor: 'white'
    }
}


export const BarChart = () => {


    const {data, labels} = useSelector(({user}:RootState) => user.chart.bar)
    const [openMonthFilter, setOpenMonthFilter] = useState(false)
    const [selectMonth, setSelectMonth] = useState('Февраль')
    const month = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь']
    const dataBar = {
        labels,
        datasets: [{
            data,
            borderWidth: 3,
            lineTension: 0,
            pointBackgroundColor: 'black',
            backgroundColor: 'rgba(94, 255, 226, 0.9)'
        }]
    }

    const selectHandlerMonth = (index: number) => {
        setSelectMonth(month[index])
    }
    
    return (
        <div className="Bar">
            <div className="Bar__inner">
                <div className="Bar__top">
                    <div className="Bar__top-name">Название графика</div>
                    <div className="Bar__top-month" onClick={() => setOpenMonthFilter(prev => !prev)}>
                        {selectMonth}
                        {openMonthFilter && <ul>
                            {month.map((item, index) => (
                                <li
                                 key={item + index}
                                 onClick={() => selectHandlerMonth(index)}
                                 >{item}</li>
                            ))}
                        </ul>}
                    </div>
                </div>
                <div className="Bar__main">
                    <Bar data={dataBar} options={options} />

                </div>
            </div>
        </div>
    )
}
