import React from 'react'
import { useSelector } from 'react-redux'
import { useHistory, useLocation } from 'react-router-dom'
import { useSwipeable } from 'react-swipeable'
import { RootState } from '../store/reducers/rootReducer'


export const Swipe: React.FC = ({ children }) => {
    const {username} = useSelector(({user}:RootState) => user)
    const pathUrl = username === 'admin' ? ['/', '/cpa', '/payouts'] : ['/', '/cpa', '/account']
    const history = useHistory()
    const location = useLocation()
    const swipe = useSwipeable({
        delta: 130,
        onSwiping: (e) => {
            console.log(e.absX);
            
        },
        onSwipedLeft: (e) => {
            if (pathUrl[pathUrl.length - 1] === location.pathname) {
                history.push(pathUrl[0])
            }
            history.push(pathUrl[pathUrl.indexOf(location.pathname) + 1])


        },
        onSwipedRight: () => {
            if (pathUrl[0] === location.pathname) {
                history.push(pathUrl[pathUrl.length - 1])
            }
            history.push(pathUrl[pathUrl.indexOf(location.pathname) - 1])
        }
    })
    
    return (
            <div {...swipe}>
                {children}
            </div>
    )
}
