interface UserReducer {
    id: null | number,
    username: null | string,
    email: null | string,
    phone_number: null | number,
    youtube_id: null | string,
    wallet: {
        id: null | number,
        provider: null | string,
        account: null | string
    }[],
    cpa: {
        id: null | number,
        title: null | string,
        terms: null | string,
        cost: null | number,
        image: null | string,
        date_from: null | string,
        date_to: null | string
    }[],
    cpaUser: string[] | null,
    transaction: {
        id: null | number,
        title: null | string,
        cost: null | number,
        date: null | string
    }[],
    chart: {
        line: {
            labels: string[] | null,
            data: number[] | null,
        },
        bar: {
            labels: string[] | null,
            data: number[] | null,
        }
    }
    isloading: boolean,
    error: null | string
}

enum userTypes {
    USER_FETCH = 'USER_FETCH',
    USER_FETCH_SUCCES = 'USER_FETCH_SUCCES',
    USER_FETCH_ERROR = 'USER_FETCH_ERROR',
    USER_SET_ADMIN = 'USER_SET_ADMIN'
}
interface userSetAdmin {
    type: userTypes.USER_SET_ADMIN,
    payload: string
}
interface userActionsTypeFetch {
    type: userTypes.USER_FETCH,
}

interface userActionsTypeSucces {
    type: userTypes.USER_FETCH_SUCCES,
    payload: any
}

interface userActionsTypeError {
    type: userTypes.USER_FETCH_ERROR,
    payload: string
}

const initilalState: UserReducer = {
    id: null,
    username: null,
    email: null,
    phone_number: null,
    youtube_id: null,
    wallet: [
        {
            id: null,
            account: null,
            provider: null
        }
    ],
    cpa: [
        {
            id: null,
            cost: 0,
            image: null,
            title: null,
            terms: null,
            date_from: null,
            date_to: null
        }
    ],
    cpaUser: null,
    transaction: [
        {
            id: null,
            cost: 0,
            date: null,
            title: null
        }
    ],
    chart: {
        line: {
            labels: ['04.03.2021', '', '', '04.03.2021', ''],
            data: [10, 20, 30, 10]
        },
        bar: {
            labels: ['04.03.2021', '04.03.2021', '04.03.2021', '04.03.2021'],
            data: [1225, 1250, 1235, 1356]
        }
    },
    isloading: false,
    error: null
}

type userActionType = userActionsTypeSucces | userActionsTypeError | userActionsTypeFetch | userSetAdmin

export const userReducer = (state = initilalState, action: userActionType): UserReducer => {
    switch (action.type) {
        case userTypes.USER_FETCH:
            return {
                ...state,
                isloading: false,
                error: null
            }
        case userTypes.USER_FETCH_SUCCES:
            return {
                ...state,
                ...action.payload,
                isloading: true,
                error: null,
            }
        case userTypes.USER_FETCH_ERROR:
            return {
                ...state,
                error: action.payload,
            }
        case userTypes.USER_SET_ADMIN:
            return {
                ...state,
                username: action.payload
            }
        default:
            return state
    }
}