import React, { useState } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import InputMask from 'react-input-mask'
import './AddIndited.scss'


export const AddIndited = () => {
    const [copyHandler, setCopyHandler] = useState(false)
    const [formInput, setFormInput] = useState({
        youtubeid: '',
        name: '',
        email: '',
        phone: ''
    })
    
    return (
        <div className="AddIndited">
            <div className="AddIndited__inner">
                <form action="" className="AddIndited__form">
                    <div className="AddIndited__form-inner">
                        <div className="AddIndited__form-title">Приглашение</div>
                        <div className="AddIndited__form-input">
                            <label htmlFor="youtube">
                                YouTube ID
                                <input id="youtube" name="youtube" type="text" placeholder="https://www.youtube.com/channel/E5Wfd1dDWSt" />
                            </label>
                        </div>
                        <div className="AddIndited__form-input">
                            <label htmlFor="name">
                                Имя
                                <input id="name" name="name" type="text" placeholder="Введите ваше имя" />
                            </label>
                        </div>
                        <div className="AddIndited__form-input">
                            <label htmlFor="email">
                                E-mail
                                <input id="email" name="email" type="text" placeholder="Введите ваш e-mail" />
                            </label>
                        </div>
                        <div className="AddIndited__form-input">
                            <label htmlFor="phone">
                                Телефон
                                <InputMask mask='+7 (999)-999-99-99' placeholder={'+7 ХХХ ХХХ ХХ ХХ'} value={formInput.phone} onChange={(e) => setFormInput(prev => {
                                    return {
                                        ...prev,
                                        phone: e.target.value
                                    }
                                })} />
                                {/* <input
                                    id="phone"
                                    name="phone"
                                    type="text"
                                    value={formInput.phone}
                                    placeholder="+7 ХХХ ХХХ ХХ ХХ"
                                    onChange={(e) => validPhoneNumber(e)}
                                /> */}
                            </label>
                        </div>
                        <button type="submit" className="AddIndited__form-submit">Получить ссылку на приглашение</button>
                        <CopyToClipboard text={'https://invitationWfd1WStd8744QUHr4'}>
                            <div
                                className="AddIndited__form-link"
                                onClick={() => setCopyHandler(true)}
                            >{copyHandler ? 'Ссылка скопирована!' : 'https://invitationWfd1WStd8744QUHr4'}</div>
                        </CopyToClipboard>
                    </div>
                </form>
            </div>
        </div>
    )
}
